import { useEffect, useMemo } from "react";
import "./App.css";
import OrderBook from "./OrderBook";
import type { Instrument } from "./OrderBook/model/orderBook";
import { createOrderBookProvider } from "./OrderBook/provider/rabbitxOrderBookProvider";
import { Centrifuge } from "centrifuge";

const DEFAULT_INSTRUMENT: Instrument = {
  code: "BTC-USD",
  baseAsset: "BTC",
  currency: "USD",
  precision: 4,
};

const configRecord = {
  prod: {
    token:
      "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiI0MDAwMDAwMDAwIiwiZXhwIjo2NTQ4NDg3NTY5fQ.o_qBZltZdDHBH3zHPQkcRhVBQCtejIuyq8V1yj5kYq8",
      wsUrl: "wss://api.prod.rabbitx.io/ws",
  },
  test: {
    token:
      "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiIwIiwiZXhwIjo1MjYyNjUyMDEwfQ.x_245iYDEvTTbraw1gt4jmFRFfgMJb-GJ-hsU9HuDik",
      wsUrl: "wss://api.testnet.rabbitx.io/ws",
  },
};

function App() {
  const config = configRecord.prod;
  
  const centrifuge = useMemo(() => new Centrifuge(config.wsUrl, {
    token: config.token,
  }), [config.token, config.wsUrl]);

  useEffect(() => {
    centrifuge.connect();
    return () => centrifuge.disconnect();
  }, [centrifuge]);

  const orderBookProvider = useMemo(() => createOrderBookProvider(centrifuge), [centrifuge]);
  return <OrderBook orderBookProvider={orderBookProvider} instrument={DEFAULT_INSTRUMENT} />;
}

export default App;
